/* ********************** Landing ***************************** */
.waveMarginBottomFix {
  margin-bottom: -2px;
}
.waveMarginTopFix {
  margin-top: -2px;
}
.landingTitle {
  font-weight: 700;
  font-size: 48px;
  line-height: 53px;
  color: #242424;
  opacity: 0.89;
}
.landingPara {
  font-weight: 500;
  font-size: 20px;
  line-height: 28px;
  color: #242424;
  opacity: 0.8;
}
.landingRightDiv {
  padding: 3rem;
}
.landingBtns {
  padding-left: 0.1rem;
}
.landingBtns button {
  border: 1px solid #242424;
  font-weight: 500;
  outline: none;
  box-shadow: none;
}
.landingBtns button:focus {
  box-shadow: none;
}
.arrowRight {
  transition: 0.2s linear;
}
.hvr-icon:hover .arrowRight {
  transform: translateX(6px);
}
@media screen and (max-width: 768px) {
  .landingTitle {
    text-align: center;
    font-size: 32px;
  }
  .landingPara {
    text-align: center;
    font-size: 16px;
  }
  .landingRightDiv {
    padding: 1rem;
    margin-top: 4rem;
  }
  .landingBtns {
    text-align: center;
  }
}

/* ************* Landing Ends **************** */
/* ***************** Count ******************** */
.countBox {
  background-image: url(data:image/svg+xml;base64,);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 120px 0px 120px 0px;
}
